// Import Firebase from npm
import { initializeApp } from "firebase/app";
import { getAuth, RecaptchaVerifier, signInWithPhoneNumber } from "firebase/auth";

const firebaseConfig = {
  apiKey: "AIzaSyAHwQXPSpGoFggaLgaWlrCtdBB7WiK7i3s",
  authDomain: "otp-project-cec6e.firebaseapp.com",
  projectId: "otp-project-cec6e",
  storageBucket: "otp-project-cec6e.appspot.com",
  messagingSenderId: "846127449397",
  appId: "1:846127449397:web:bca5564a01535de23c387c",
  measurementId: "G-FSSK8JET3M"
};

// Initialize Firebase
const app = initializeApp(firebaseConfig);
const auth = getAuth(app);

// Send OTP
function sendVerificationCode() {
  const phoneNumber = document.getElementById("phoneNumber").value;

  if (!phoneNumber) {
    alert("Please enter a phone number");
    return;
  }

  // Set up reCAPTCHA
  const recaptchaVerifier = new RecaptchaVerifier('recaptcha-container', {
    'size': 'normal',
    'callback': function(response) {
      // reCAPTCHA solved, allow user to send verification code
      console.log("reCAPTCHA solved");
    }
  }, auth);

  // Send OTP
  signInWithPhoneNumber(auth, phoneNumber, recaptchaVerifier)
    .then(function(confirmationResult) {
      window.confirmationResult = confirmationResult;
      alert("OTP sent!");
    }).catch(function(error) {
      console.error("Error during signInWithPhoneNumber", error);
      alert("Error sending OTP: " + error.message);
    });
}

// Verify OTP
function verifyCode() {
  const code = document.getElementById("verificationCode").value;
  if (!code) {
    alert("Please enter the verification code");
    return;
  }

  if (!window.confirmationResult) {
    alert("Please send the verification code first");
    return;
  }

  window.confirmationResult.confirm(code).then(function(result) {
    // User signed in successfully.
    const user = result.user;
    alert("Phone number verified and user signed in!");
  }).catch(function(error) {
    console.error("Error during code verification", error);
    alert("Error verifying code: " + error.message);
  });
}